html,
body {
  width: 100% !important;
  overflow-x: hidden !important;
}
.visualizer-container {
  width: 100% !important;
  max-width: none !important;
  overflow-x: visible !important;
}
.strum-container {
  width: 100% !important;
  max-width: none !important;
  min-width: 100% !important;
  overflow-x: visible !important;
  justify-content: center !important;
}
.strum-node {
